.bilimdent-kids {
  padding: 100px 40px;
}
.bilim-dent-kids-header {
  margin-bottom: 100px;

  font-size: 52px !important;
  text-align: center;
  font-weight: 500;

  span {
    color: #ba9e66;
  }
}
.bilimdent-kids-container {
  display: flex;
  flex-direction: column;
  gap: 40px;
  // text-align: center;
}
.bilimdent-kids-inner {
  display: flex;
  // text-align: center;
  gap: 30px;
  padding: 40px;
  align-items: center;
  // border: 1px solid #000;
  transition: all 0.5s;
}
.bilimdent-kids-desc::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8));
}
.desc-image-1 {
  background-position: center;
  background-image: url(../../../assets/kids/pedadontics.jpg);
}
