  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  position: relative;
  // gap: 40px;
  p {
    // text-align: center;
  }
}
.bilimdent-kids-desc {
  background-size: cover;
  background-position: center;
  position: relative;
  z-index: 5;
  padding: 40px;
  color: #fff;
  h3 {
    font-size: 48px !important;
    font-weight: 400;
    text-align: center;
    // background: linear-gradient(
    //   rgba(244, 244, 244, 0.508),
    //   // rgba(186, 158, 102, 0.333),
    //   // rgba(186, 158, 102, 0.331),
    //   rgba(255, 255, 255, 0.508)
    // );
    padding: 0px;
    // background-color: rgba(255, 255, 255, 0.995);
    // background-color: rgba(186, 158, 102, 1);
  }
  p {
    // color: #777777;
    font-family: "Proza Libre", sans-serif;
    font-size: 24px;
    line-height: 2;
    // background-color: rgba(186, 158, 102, 1);
    // color: #fff;
    // background-color: rgba(255, 255, 255, 0.65);
  }
}
.bilimdent-kids-outer {
  display: flex;
  flex-direction: column;
  gap: 40px;
  overflow-x: hidden;
  .bilimdent-kids-outer-head {
    font-size: 48px;
  }
}

.bilimdent-kids-outer-description {
  display: flex;
  // margin-left: 70px;
  align-items: center;
  font-size: 24px;
  font-weight: 400;
  // gap: 60px;
  font-family: "Proza Libre", sans-serif;
  letter-spacing: -0.1px;
  div {
    display: flex;
    // background-color: blue;
    margin-left: 30px;
    margin-right: 30px;
  }
}
.bilimdent-kids-outer-desc {
  font-size: 24px;
  line-height: 1.7;
  color: #000;
  font-family: "Proza Libre", sans-serif;
}
@keyframes goround {
  from {
    transform: translateX(40%);
  }
  to {
    transform: translateX(-85%);
  }
}
.bilimdent-kids-outer-container {
  width: 4060px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
  // background-color: red;
  // transform: translateX(40%);
  // transform: translateX(-85%);

  animation: goround 30s linear infinite;
}

