.home-section-3 {
  padding: 100px 0px 80px;
  vertical-align: middle;
  .section-3-heading {
    padding-bottom: 40px;
    font-size: 52px;
    text-align: center;
    font-weight: 700 !important;
  }
}
.home-section-3-wrap {
  // padding-bottom: 120px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  .section-3-description {
    margin-bottom: 4px;
  }
  .section-3-list {
    display: flex;
    gap: 60px;
    font-weight: 500;
    div {
      line-height: 2;
    }
    li {
      list-style-type: none;
      font-size: 28px;
      display: flex;
      align-items: center;
      gap: 20px;
    }
  }
}
.section-3-image-container {
  width: 500px;
  height: 510px;
  display: flex;
  align-items: flex-start;
  padding-bottom: 54px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}
.section-3-info-button {
  display: none;
  cursor: pointer;
  font-size: 28px;
  font-weight: 600;
  margin-top: 49px;
  padding: 20px;
  text-align: center;
  background-color: #ba9e66;
  position: relative;
  color: #fff;
  z-index: 1;
}
.section-3-info-button::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scaleY(0);
  transform-origin: center bottom;
  background: #000;
  z-index: -1;
  transition: transform 0.3s;
}
.section-3-up-down-btn {
  display: none;
}
.section-3-info-button:hover::after {
  color: #fff;
  transform: scaleY(1);
}
