.home-section-6 {
  padding: 60px 50px 100px;
  background-color: #fff;
  // background-color: #000;
  // color: #fff;
  color: #000;
}
.home-section-6-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;
}
.home-section-6-button-left {
  display: none;
}
.home-section-6-button-right {
  display: none;
}
.home-section-6-heading {
  font-size: 52px;
}
.home-section-6-container {
  position: relative;
  display: flex;
  align-items: center;
  height: 400px;
  // overflow: auto;
}
.home-section-6-user-reviews {
  position: relative;
  // overflow: auto;

  .home-section-6-user-reviews-inner {
    // width: 1300px;
    // display: grid;
    // grid-template-columns: 1fr;
    display: flex;
    // flex-wrap: wrap;
    margin-top: 20px;
    align-items: center;

    // justify-content: center;
    gap: 30px;
  }
  &.home-section-6-user-reviews::-webkit-scrollbar {
    // display: none;
    width: 0px;
  }
}
.home-section-6-user-reviews-container {
  // background-color: #000;
  border: 1px solid #000;
  width: 200px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #000;
  // color: #fff;
  // background-color: #fff;
  padding: 30px 20px;
  gap: 20px;
  cursor: pointer;
  transition: all 0.8s;
}
.section-6-star {
  margin-right: 14px;
}
.home-section-6-user-reviews-container:hover {
  // transform: scale(1.1);
}
.home-section-6-user-reviews-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.home-section-6-user-reviews-image {
  display: flex;
  gap: 20px;
}
.home-section6-user-reviews-img {
  display: flex;
  align-items: center;
}
.home-section-6-user-name {
  display: flex;
  flex-direction: column;
  font-size: 20px;
  width: 120%;
}
.section-6-months {
  font-size: 14px;
  color: #777777;
}
.section-6-description {
  overflow: auto;
  padding: 10px;
}

.section-6-description::-webkit-scrollbar {
  width: 4px;
  background-color: #fff;
  border-radius: 1px;
}
.section-6-description::-webkit-scrollbar-thumb {
  width: 4px;
  background-color: #000;
}
.section-6-description::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.502);
  background-color: #fff;
}

