    // gap: 40px !important;
  }
  .home-section-6-button-right {
    position: absolute;
    display: flex;
    justify-content: center;
    top: 45%;
    right: -4%;
    width: 40px;
    height: 40px;
    align-items: center;
    color: #fff;
    z-index: 1;
    background-color: #ba9e66;
  }
  .home-section-6-button-left {
  }

  .home-section-6-user-reviews {
    position: relative;
    width: 90vw;
    overflow: auto;
    // overflow: hidden;
    scroll-behavior: smooth;
  }
  .home-section-6-user-reviews-inner {
    width: 1440px;
    gap: 60px !important;
    transform: translateX(-50%);
    // animation: animat 50s linear infinite;
  }
  .home-section-6-user-reviews-container {
    width: 200px;
    height: 200px !important;
  }
  .home-section-6-user-name {
    font-size: 18px !important;
  }
  .section-6-months {
    font-size: 12px !important
    ;
  }
  @keyframes animat {
    from {
      transform: translateX(0%);
    }
    to {
      transform: translateX(-80%);
    }
  }
}
