.home-section-4 {
  padding: 100px 40px 100px;
  // background-color: #000;
  align-items: center;
  position: relative;
}
.home-section-4-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  // gap: 80px;
}
.section-4-frame {
  position: relative;
  width: 1300px;
  height: 500px;
  // border-radius: 2px;
  // box-shadow: 1px 2px 10px 0px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  background: #333;
  color: #fff;
}
.section-4-inner {
  width: 100%;
  height: 100%;
  transition: 1s all;
  // transform: translateY(-500px);
}
.gap {
  gap: 0px !important;
}
.section-4-boxes {
  background: #fff;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  width: 100%;
  height: 500px;

  .box {
    position: relative;
    width: 20%;
    transition: all 1s ease-in-out;
    cursor: pointer;
    overflow: hidden;
  }
  .section-4-img-1 {
    background: url(../../../../assets/gallery-1.jpg);
    background-size: cover;
    // background-size: contain;
    background-position: center;
  }
  .section-4-img-2 {
    background: url(../../../../assets/gallery-2.jpg);
    background-size: cover;
    // background-size: contain;

    background-position: center;
  }
  .section-4-img-3 {
    background: url(../../../../assets/gallery-3.jpg);
    background-size: cover;
    // background-size: contain;
    background-position: center;
  }
  .section-4-img-4 {
    background: url(../../../../assets/gallery-4.jpg);
    background-size: cover;
    // background-size: contain;

    background-position: center;
  }
  .section-4-img-5 {
    background: url(../../../../assets/gallery-5.jpg);
    background-size: cover;
    // background-size: contain;

    background-position: center;
  }
  .section-4-img-6 {
    background: url(../../../../assets/gallery-6.jpg);
    background-size: cover;
    // background-size: contain;

    background-position: center;
  }
}
.section-4-button-hidden {
  opacity: 0;
  height: 0;
  visibility: hidden;
  pointer-events: none;
}
.section-4-button {
  padding: 20px 80px;
  cursor: pointer;
  color: #fff;
  position: absolute;
  bottom: 40%;
  font-size: 24px;
  background-color: #ba9e66;
  // border: 1px solid #ba9e66;
  z-index: 1;
  transition: 1s background-color, 0.3s color, opacity 1s;
  // &:hover {
  //   background-color: #ba9e66;
  //   color: #fff;
  // }
}
.section-4-button:hover {
  background-color: transparent;
  // border: 2px solid #ba9e66;
  background-color: #000;
}
