.hoverfunc:hover {
  width: 100%;
  cursor: url(../../../../assets/plus.svg), nesw-resize;
}
.home-section-4-i:hover {
  // transform: scale(1.01);
}
.section-4-image-container {
  max-width: 1300px;
  height: 100%;
  background-color: #fff;

  .section-4-image {
    // height: 100% ;
    cursor: pointer;
    overflow: hidden;
    white-space: nowrap;
    scroll-behavior: smooth;
    // touch-action: none;
    // overflow-y: hidden !important;

    &.dragging {
      cursor: grab;
      scroll-behavior: auto;
      .dragging img {
        pointer-events: none;
      }
    }
    img {
      object-fit: contain;
      user-select: none;
      width: calc(100% / 2);

      height: 100%;
      // width: 100%;
      object-position: center;
    }
  }
}
.section-4-exit {
  position: absolute;
  bottom: -13.5%;
  right: 0%;
  height: 40px;
  // width: 80px;
  padding: 14px;
  svg {
    width: 100%;
    height: 100%;
    transition: 0.5s all;
  }
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  background-color: #ba9e66;
  &:hover {
    color: #000;
  }
}
.section-4-details {
  position: absolute;
  bottom: -97.1%;
  left: 38.3%;
  width: 300px;
  height: 40px;
  // background-color: #ba9e66;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .section-4-details-button-left {
    background-color: #ba9e66;
    height: 40px;
    padding: 14px;
    cursor: pointer;
    svg {
      transition: 0.5s all;
    }
    &:hover {
      svg {
        color: #000;
      }
    }
  }

  .section-4-details-button-right {
    cursor: pointer;
    padding: 14px;
    height: 40px;
    background-color: #ba9e66;
    // text-align: center;
    transition: 0.5s all;
    svg {
      transition: 0.5s all;
    }
    &:hover {
      svg {
        color: #000;
      }
    }
  }
  .section-4-details-num {
    font-size: 24px;
    padding: 20px;
    background-color: #ba9e66;
    color: #fff;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
      sans-serif;
  }
}
